import { getPreloadedUserStatus } from "../utils/userStatus.ts";
const supportsAvatar = hasTalkFeature("local", "avatar");
export default {
  name: "ConversationIcon",
  components: {
    IconStar,
    IconVideo,
    NcAvatar
  },
  props: {
    /**
     * Allow to hide the favorite icon, e.g. on mentions
     */
    hideFavorite: {
      type: Boolean,
      default: true
    },
    hideCall: {
      type: Boolean,
      default: true
    },
    disableMenu: {
      type: Boolean,
      default: true
    },
    hideUserStatus: {
      type: Boolean,
      default: false
    },
    showUserOnlineStatus: {
      type: Boolean,
      default: false
    },
    item: {
      type: Object,
      default() {
        return {
          objectType: "",
          type: 0,
          displayName: "",
          isFavorite: false
        };
      }
    },
    /**
     * Reduces the opacity of the icon if true
     */
    offline: {
      type: Boolean,
      default: false
    },
    size: {
      type: Number,
      default: AVATAR.SIZE.DEFAULT
    }
  },
  setup() {
    const isDarkTheme = useIsDarkTheme();
    const failed = ref(false);
    function onError() {
      failed.value = true;
    }
    return {
      isDarkTheme,
      failed,
      onError
    };
  },
  computed: {
    showCall() {
      return !this.hideCall && this.item.hasCall;
    },
    showFavorite() {
      return !this.hideFavorite && this.item.isFavorite;
    },
    preloadedUserStatus() {
      if (this.hideUserStatus) {
        return void 0;
      }
      return getPreloadedUserStatus(this.item);
    },
    iconClass() {
      return getFallbackIconClass(this.item, this.failed);
    },
